.result-card {
  padding: 10px;
}
.search-box {
  background-color: white;
  cursor: pointer;
  border: 0 solid hsla(0, 0%, 100%, 0.25);
  border-radius: 2px;
  box-shadow: 0 1px 5px #dcdcdc;
  transition: all 0.2s ease-in-out;
  padding: 15px;
  height: auto;
  overflow: hidden;
  width: 100%;
}
.user-avatar img {
  max-height: 120px;
  height: 100%;
  float: left;
}
.details {
  display: inline-block;
  padding: 0 10px;
  width: calc(100% - 130px);
}
.search-box .details .date {
  color: #f4645f;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 14px;
  vertical-align: top;
}
.search-box .details .job {
  color: rgba(150, 150, 150, 0.9);
  font-size: 11px;
  font-family: "Open Sans";
}
.search-box .name {
  overflow: hidden;
  text-overflow: ellipsis;
}
.search-box:hover {
  box-shadow: 0 5px 5px #dcdcdc;
  top: -5px;
}
.search-box .details .description {
  font-size: 11px;
  vertical-align: top;
  line-height: 1;
}
.search-box hr {
  margin: 8px 0;
}
@media (max-width: 380px) {
.search-box,
  .result-card {
    width: max-content;
    display: inline-block;
}
.search-box .details .name {
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    max-width: 130px;
}
.search-box .details {
    font-size: 12px;
    width: calc(100% - 100px);
}
.user-avatar img {
    max-height: 75px;
}
